*, *::after, *::before {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  padding: 5% 20%;
  font-family: 'Open Sans', sans-serif;
  background-color: rgb(245, 245, 245);
  position: relative;
  overflow-x: hidden;
}

::-webkit-scrollbar {
  width: 12px;
}

::-webkit-scrollbar-track {
  background: #ddd;
}

::-webkit-scrollbar-thumb {
  background: #222;
  border-radius: 5px 5px;
}

a, a:link, a:visited {
  text-decoration: inherit;
  color: inherit;
}

@media screen and (max-width: 768px){
  body {
    padding: 20% 10%;
    font-family: 'Open Sans', sans-serif;
    background-color: rgb(245, 245, 245);
    position: relative;
    overflow-x: hidden;
  }
}